import React from 'react';
import './FindMovie.scss';
import classNames from 'classnames';

// import { Movie } from '../../types/Movie';
import { MovieData } from '../../types/MovieData';
import { getMovie } from '../../api';
import { MovieCard } from '../MovieCard/MovieCard';

type Props = {
  addNewMovie: (movie: MovieData | null) => void,
};

export const FindMovie: React.FC<Props> = ({ addNewMovie }) => {
  const [movie, setMovie] = React.useState<MovieData | null>(null);
  const [movieTitleQuery, setMovieTitleQuery] = React.useState('');
  const [hasLoadingError, setHasLoadingError] = React.useState(false);
  const [loadingMovie, setLoadingMovie] = React.useState(false);

  const handleLoadMovieFromServer = async () => {
    setLoadingMovie(true);

    try {
      const newMovie = await getMovie(movieTitleQuery);

      if (!newMovie.imdbID) {
        setMovie(null);
        setHasLoadingError(true);
      } else {
        setMovie(newMovie);
        setHasLoadingError(false);
      }
    } catch (error) {

      // if (error.status === 404) {
      //   setNewMovie(null);
      // }
    } finally {
      setLoadingMovie(false);
    }
  };

  const handleFindMovie = (query: string) => {
    if (query !== movieTitleQuery) {
      setHasLoadingError(false);
    }

    setMovieTitleQuery(query);
  };

  return (
    <>
      <form
        className="find-movie"
        onSubmit={event => event.preventDefault()}
      >
        <div className="field">
          <label className="label" htmlFor="movie-title">
            Movie title
          </label>

          <div className="control">
            <input
              data-cy="titleField"
              type="text"
              id="movie-title"
              placeholder="Enter a title to search"
              className="input is-dander"
              value={movieTitleQuery}
              onChange={(event) => handleFindMovie(event.target.value)}
            />
          </div>

          {hasLoadingError && (
            <p className="help is-danger" data-cy="errorMessage">
              Can&apos;t find a movie with such a title
            </p>
          )}

        </div>

        <div className="field is-grouped">
          <div className="control">

            <button
              disabled={!movieTitleQuery}
              data-cy="searchButton"
              type="submit"
              className={classNames(
                'button is-light',
                { 'is-loading': loadingMovie },
              )}
              onClick={handleLoadMovieFromServer}
            >
              {!movie
                ? 'Find a movie'
                : 'Search again'}
            </button>

          </div>

          <div className="control">
            {movie && (
              <button
                data-cy="addButton"
                type="button"
                className="button is-primary"
                onClick={() => {
                  addNewMovie(movie);
                  setMovie(null);
                  setMovieTitleQuery('');
                }}
              >
                Add to the list
              </button>
            )}
          </div>
        </div>
      </form>

      {movie && (
        <div className="container" data-cy="previewContainer">
          <h2 className="title">Preview</h2>
          <MovieCard movie={movie} />
        </div>
      )}
    </>
  );
};
